.fifth-section {
    margin-top: 5em;
}

.formContainer {
    padding: 20px;
    border-radius: 7px;
}

.successImg{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-70%);
    width: 100%;
    z-index: -1;
}

.submitPage {
    position: absolute;
    width: 100%;
    height: 100vh;
    left: 0;
    top: 0;
    z-index: 101;
    background: #CD6464;
}