nav {
  position: fixed;
  width: 100%;
  height: 4em;
  background: var(--navbar-background);
  top: 0;
  z-index: 100;
  -webkit-box-shadow: 0px 1px 14px 3px #000000; 
box-shadow: 0px 0px 8px 0px #000000;
}
ul{
    margin: 0;
    padding: 0;
}

li {
  list-style: none;
}
a {
  text-decoration: none;
  color: white;
  font-size: 0.97rem;
  font-weight: lighter;
  letter-spacing: 0.5px;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  padding: 10px 10px 10px 10px;
}
.nav-links{
    cursor: pointer;
    
}
.nav-links:hover{
    color: var(--primary);
    padding-top: 22px;
    padding-bottom: 20px;
    border-bottom: 2px solid var(--primary);
}

.navbar-cart-icon{
    padding-left: 0.3em;
}

.items-nav{
    padding-left: 1em;
}
.navbar-items{
    display: flex;
    justify-content: center;
    align-items: center;
}
.navbar-content-elements {
  display: flex;
  justify-content: space-between;
  align-items: center;
}


/* Logo App */
.navbar-logo{
    font-size: 1.5rem;
    letter-spacing: 2px;
    font-weight: 400;
    cursor: pointer;
    user-select: none;
  -webkit-tap-highlight-color: transparent;
}
.and{
    color: var(--primary);
    font-size: 2rem;
    font-weight: bold;
}

.nav-links.active{
    color: var(--primary);
    padding-top: 22px;
    padding-bottom: 20px;
    border-bottom: 2px solid var(--primary);
}

/* Menu icon */

.menu-icon {
    display: none;
}




@media screen and (max-width: 1200px){
    .navbar-items{
        position: fixed;
    }
    .nav-menu{
        width: 100%;
        height: auto;
        position: absolute;
        top: 64px;
        left: -150%;
        opacity: 1;
        transition: all 0.5s ease;
    }

    .nav-menu.active{
        background: black;
        left: 0;
        width: 100%;
        opacity: 1;
        transition: all 0.5 ease;
        z-index: 1;
    }

    .nav-links{
        padding: 1rem;
        width: 100%;
        display: table;
    }
    .nav-links:hover{
        border: none;
        padding: 1rem;
    }
    .navbar-logo {
        position: absolute;
        top: 0;
        left: 50%;
        user-select: none;
        -webkit-tap-highlight-color: transparent;
        transform: translate(-50%, 30%);
    }

    .menu-icon{
        display: block;
        cursor: pointer;
        -webkit-tap-highlight-color: transparent;
    }

    .item-container{
        background: white;

    }

    .navbar-cart-icon:hover{
        background: rgb(37, 37, 37);
    }

    .icon-shop{
        color: white;
    }

    .navbar-cart-icon{
        width: 100%;
        margin: 0;
        padding: 0;
        text-align: center;
    }

    .items-nav{
        padding: 0;
        width: 100%;
    }
    .items-nav:hover{
        background: rgb(37, 37, 37);
    }
    .nav-links.active{
        padding-top: 16px;
        padding-bottom: 16px;
        border: none;
    }
    
    
    
}

