.highlight{
    background: var(--primary);
    padding: 5px;
}

.carousel-root {
    width: 100% !important;
    margin: auto !important;
    margin-top: 3% !important;
    background: var(--primary);
}

.carousel .slide {
    background: transparent !important;
    height: 100%;
}
.containerOfElements {
  padding: 1.5rem;
}
.carousel .slide .guysTestimonial{
  width: 120px !important;
  border-radius: 50%;
}
.imgPattern{
  position: absolute;
  background-repeat: repeat;
  opacity: 0.05;
  z-index: -1;
}

.myCarousel {
    background: transparent;
    margin-top: -6%;
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    padding-top: 6%;
    padding-bottom: 8%;
    padding-left: 5%;
    padding-right: 5%;
    height: 360px;
  }

  .carousel .control-dots {
    padding-left: 5px !important;
    outline: 0;
    bottom: 5% !important;
  }

  .carousel .control-dots .dot {
    box-shadow: none !important;
    background: #000 !important;
    outline: 0;
  }
  
  .carousel.carousel-slider .control-arrow {
    background: #000 !important;
    height: 40px !important;
    position: absolute;
    top: 35% !important;
  }